@use '../base/variables' as v;
@use '../base/mixins' as m;


.main {
  position: relative;
 
  margin-top: -3rem;
  @include m.tablet {
    margin-top: -10rem;
  }


 
}
.main__contenido {
@include m.box-shadow;
  padding: 5rem 2.5rem ; /* cambiar*/
  line-height: 1.8;
  background-color: rgb(255, 255, 255);


  @include m.tablet {
    padding: 7rem ;
  }
}



.nosotros__titulo {
    text-transform: uppercase;
    color: v.$cafe;
    margin-bottom: 5rem;

    &::after {
      content: '';
      background-image: url(../img/cafe.svg);
      width: 12rem;
      display: block;
      height: 10rem;
      background-repeat: no-repeat;
      background-size: 12rem;
      margin: 0 auto;
      
    }

    
    .nosotros__span{
      display: block;
      color:v.$amarillo;
      font-size: 2rem;
      margin-bottom: 1rem;
    }

    
  }

  .nosotros__parrafo {  
    text-align: justify;

    @include m.tablet {
      text-align: center;
    }
  }

.iconos {
  padding: 4rem 0;

  @include m.tablet {
    @include m.grid(3, 3rem);
    justify-items: center;
  }

}


.icono__imagen {
  width: 15rem;
  margin: 0 auto;
}

.icono__nombre {
  text-align: center;
  color:v.$cafe;
  font-weight: bold;
}