@use '../base/variables' as v;
@use '../base/mixins' as m;


.menu {
  box-shadow: -1px -3px 20px -7px rgba(84,39,22,1);
  color: v.$blanco;;
  height: 100%;
  background-image: url(../img/menu_arriba.svg),url(../img/menu_abajo.svg),linear-gradient(0deg,#4c2110,#502313);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center 5rem,center 100%,100%;

  @include m.pc {
    background-size: 60%,60%,100%;
  }
 
}

.contenido__menu {
  padding: 20rem 1rem;
}

.menu__grid {
  @include m.tablet {
    @include m.grid(2, 7rem);
  }
}

.menu__title {
    text-transform: uppercase;

  span {
    font-size: 2.2rem;
    display: block;
  }
}

.seccion {
  padding: 2rem 0;;
 ul {
    display: block;
    padding-left: 0;
  }
}


.seccion__title {
  padding: .5rem 2rem;
  border-radius:  0  0 7px 7px ;
  border-bottom: 1px double rgba(129, 129, 130, 0.429);
  text-transform: uppercase;
  font-family:'Times New Roman', Times, serif;
  display: inline-block;
}

.producto {
  border-radius: 30px 5px;
  padding: 1rem;
  border: 1px dotted v.$amarillo;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.producto__nombre {
  margin-top: 0 ;
  font-size: 2.5rem;
  display: block;
  text-align: left;


  span {
    font-weight: normal;
    font-size: 2rem;
    display: block;
    color:v.$amarillo;
  }
}

.producto__precio {
  font-family: cursive;
  font-weight: bold;
  font-size: 3.4rem;
}

