@use 'variables' as v;


@mixin phone {
  @media(min-width: v.$phone){
    @content;
  }
}
@mixin tablet {
  @media(min-width: v.$tablet){
    @content;
  }
}

@mixin pc{
  @media(min-width: v.$pc){
    @content;
  }
}

@mixin monitor{
  @media(min-width: v.$monitor){
    @content;
  }
}


//display grid
@mixin grid($columnas,$gap){
  display:grid;
  grid-template-columns: repeat($columnas,1fr);
  gap:$gap;
}

@mixin box-shadow {
  box-shadow: -3px -23px 24px -12px rgba(0,0,0,0.75);
  
}

