@use '../base/variables' as v;
@use '../base/mixins' as m;



.testimonios__contenido {
  @include m.box-shadow;
  margin-top:-15rem;
  padding: 7rem 0;
  background-color: #fff;
}

.testimonios__grid{

  @include m.tablet {
    @include m.grid(2, 2rem);
  }
}

.testimonio{
  margin-bottom: 2.5rem;
  border-radius: 5px;
  padding: 2rem;
  background-color:v.$tarjetas;

  @include m.tablet {
    margin-bottom: 0;
  }
}

.testimonio__info{
  text-align: justify;
}

.testimonio__autor {
  font-family: v.$fuente_principal;
  margin-bottom: 0;
  font-size: 2rem;
  font-weight: bold;
   color:v.$cafe;
  text-align: right;
}