@use '../base/mixins' as m;
@use '../base/variables' as v;



.procesos__grid {

}

.proceso {
  margin-bottom: 4rem;
  @include m.tablet {
    @include m.grid (2, 2.5rem);
  }

}

.proceso:nth-child(2){
  .proceso__imagen {
    grid-column: 2/3;
  }
  .proceso__info {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}

.proceso:nth-child(4){
  .proceso__imagen {
    grid-column: 2/3;
  }
  .proceso__info {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}

.proceso:nth-child(6){
  .proceso__imagen {
    grid-column: 2/3;
  }
  .proceso__info {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}



.proceso__imagen {

}


.proceso__info {

}

.info__titulo {
  text-align: left;
  color: v.$cafe;
  text-transform: uppercase;

  &::after {
    content: '';
    display: block;
    background-color: v.$amarillo;
    width: 30%;
    height: 5px;
    border-radius:  0 20px 20px 0;
  }
}
