@use '../base/variables' as v;
@use '../base/mixins' as m;



.experiencia__grid {
  box-shadow: -3px 1px 19px 1px rgba(72, 35, 7, 0.5);
    @include m.grid(2, 0);

    @include m.pc {
     @include m.grid(4,0);
    }
  
}

.experiencia__imagen {


  img {
    border:1px solid rgb(84, 41, 8);
    margin:0 auto;
    height:25rem;
    object-fit: cover;

    @include m.pc{
      
      display:block;
      width: 100%;
      height: 100%;
    }
  }

 
}

@include m.pc {
  .experiencia__imagen:first-child {
    grid-row: 1/3;
  }

  .experiencia__imagen:nth-child(2 ){
    grid-column: 2/4;
  }

  .experiencia__imagen:nth-child(4){
    grid-row: 2/4;
  }

  .experiencia__imagen:nth-child(5){
    grid-column: 3/5;
  }

  .experiencia__imagen:nth-child(6 ){
    grid-column: 3/5;
  }
  .experiencia__imagen:nth-child(7){
    grid-row: 3/5;
  }
  .experiencia__imagen:nth-child(8){
    grid-column: 2/5;
  }
  .experiencia__imagen:nth-child(9 ){
    grid-column: 1/3;
  }
  .experiencia__imagen:nth-child(10 ){
    grid-column: 3/5;
  }

}