@use '../base/variables' as v;
@use '../base/mixins' as m;



.contenido__contacto {
  @include m.tablet {
    @include m.grid(2, 0);
    background-image: url(../img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  };
}



.form__flex {
  margin-bottom: 2rem;
  @include m.tablet {
    @include m.grid (2, 2rem);
    align-items: center;

    select, input {
      border-radius: 40px;
    }
  }
}
.campo {
  margin-bottom: 1.5rem;


  label {
    text-align: center;
    font-size: 2.3rem;
    font-weight: 900;
    display: block;
    text-transform: uppercase;
   
  }

  input:not([type="submit"]), select {
    border-radius:  0 5rem 0  5rem;
    text-align: center;
     width: 90%;
     padding: 1.2rem;
     border:3px double v.$cafe;
 
 
   }
  
  input[type="submit"]{
    margin: 0 auto;
    width: 90%;
    text-transform: uppercase;
    font-size: 2.5rem;
    display: block;
    background-color: v.$cafe;
    color: v.$blanco;
    padding: 1rem;
    border: none;
    border-radius: 3px;
    cursor: pointer;

  }

  

 

}
