@use '../base/variables' as v;
@use '../base/mixins' as m;




.nosotros__grid {
  @include m.tablet {
    @include m.grid(2, 6rem);
    align-items: center;
  }
}

.nosotros__imagen {
  box-shadow: 9px 10px 5px -7px rgb(0 0 0 / 35%);
  border: 1rem solid v.$blanco;
  transform: rotate(-6deg);
  margin-bottom: 5rem;
}

.texto__info {
  margin:1rem ;
}

.texto__blockquote{
  font-size: 2rem;
  text-align: left;
  margin: 0;
  padding: .7rem;
  border-left:  3px solid v.$cafe;
  border-radius: 4px;
  
}