@use '../base/variables' as v;
@use '../base/mixins' as m;


.footer {
  background-color: v.$cafe;
}



.footer__grid {
  padding-top: 4rem;
  background-color: rgb(255, 255, 255);
}

.box{
  background-color: v.$tarjetas;
  width: 30rem;
  padding: 2rem 0;
  margin-bottom: 1rem;
  display: inline-block; 
  border: 1px dotted v.$cafe;
  border-radius: 5px;
}

.box__title {
  text-transform: uppercase;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: #5e4307;
  padding: .2rem 2rem;

}

.box__info{
  text-align: center;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  margin-bottom: 1rem;
  margin-top: 0;
}

.footer__derechos {
  text-align: center;
  color: v.$blanco;
  margin: 0;
  padding: 2rem 0;
}