@use '../base/variables' as v;
@use '../base/mixins' as m;


.header {
  padding: 3rem 1rem;
  position: relative;
  height: 90rem;
  background-repeat: no-repeat;
  background-size: cover;


  &::before {
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    content:'';
    background-color: v.$negro;
    opacity: .6;

  }

  @include m.tablet {
    height: 65rem;
  }
}

.header__index {
  background-image: url('../img/header_inicio.jpg');
}

.header__contenido {
  position: relative;
  color:v.$blanco;
  display: flex;
  flex-direction: column;
  gap:9rem;

  @include m.tablet {
    gap:20rem;
  }

  
}

.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;

  @include m.tablet{
    display: flex;
    flex-direction: row;
    justify-content: space-between;

  }

}

.header__logo {
  width: 25rem;

  @include m.tablet {
    width:20rem;
  }
}

.header__nav {
  display: flex;
  flex-direction: column;
  gap:2rem;

  @include m.tablet {
    flex-direction: row;
   
  }
  
}



.enlace:hover {
  color:v.$amarillo;
}


.header__mensaje {
  text-transform: uppercase;
  
}